.item {
  display: block;
  margin: 5px;
  background-color: #cf5b28;
  color: #311a14;
  border-radius: 4px;
  padding: 5px;
  height: min-content;
  min-width: 50px;
  white-space: pre-line;
  word-wrap: break-word;
  max-width: 70vw;
}

@media (max-width: 767px) {
  .item {
    font-size: 15px;
  }
}

@media (max-width: 470px) {
  .item {
    max-width: 55vw;
    font-size: 14.5px;
  }
}
@media (max-width: 310px) {
  .item {
    max-width: 40vw;
    font-size: 13px;
  }
}
