@import "../../variables.module.scss";

.dialogs {
  & input {
    width: 136px;
  }
}

.dialogsItems_dark {
  background-color: $darkBackgroundInputColor;
  height: min-content;
  font-size: 19px;
  min-width: 170px;
  margin-bottom: 10px;
  margin-top: 10px;
  border-radius: 5px;
  & a {
    color: black;
  }
  width: 18%;
}
.dialogsItems_light {
  height: min-content;
  font-size: 19px;
  min-width: 170px;
  margin-bottom: 10px;
  margin-top: 10px;
  border-radius: 5px;
  background-color: white;
  & a {
    color: black;
  }
  width: 18%;
}

/*.dialogsMain {
  height: calc(100vh - 70px);
  min-height: 244px;
}*/

/*  align-items: center;
  align-content: center;
  justify-content: center;*/

.dialog {
  height: calc(100vh - 210px);
  display: flex;
  align-items: flex-end;
  justify-content: left;
  left: 3%;
  position: absolute;
}

.messages {
  float: right;
  height: calc(100vh - 250px);
  display: flex;
  align-items: flex-end;
  flex-direction: column-reverse;
}

@media (max-width: 767px) {
  .dialogsItems_light {
    width: 170px;
  }
  .dialogsItems_dark {
    width: 170px;
  }
}
