.item {
  grid-area: "message";
  display: grid;
  width: 100%;
  margin-top: 10px;
  grid-template-areas: "a";
  grid-template-columns: 65px 1fr;
}

.avatar {
  width: 60px;
  height: 60px;
  border-radius: 20px;
  grid-area: a;
  margin-top: 5px;
}

.form_message input {
  width: 40vw;
  height: 30px;
}

.formSummaryError {
  color: red;
  width: 250px;
  margin: 0;
  font-family: verdana;
  font-size: 12px;
}
